.steps-component {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}
.steps-component .steps-navigator {
  display: flex;
  justify-content: center;
  list-style-type: none;
  position: absolute;
  bottom: -90px;
  width: 100%;
  padding: 0;
}
.steps-component .steps-navigator li {
  display: inline-block;
  width: 0.3em;
  height: 0.3em;
  background: #d3d3d3;
  margin: 0.3em;
  transform: rotate(45deg);
  border: 1px solid #d3d3d3;
}
.steps-component .steps-navigator li.active-step {
  background: #fff;
  border: 1px solid #008000;
}
.steps-component .steps-content {
  width: 100%;
  box-sizing: border-box;
}
.steps-component .steps-content .form-title {
  color: #424242;
  font-weight: bold;
  font-size: 12px;
}
.steps-component .steps-content .form-data {
  width: 100%;
  float: left;
  clear: both;
}
.steps-component .steps-content .form-data label {
  float: left;
  clear: both;
  font-size: 14px;
  margin: 21px 0 5px;
  color: #525151;
}
.steps-component .steps-content .form-data input {
  width: 100%;
  float: left;
  clear: both;
  padding: 7px;
  font-size: 18px;
  box-sizing: border-box;
  outline: none;
}
.steps-component .steps-content .finish-button {
  width: 100px;
  background: #f00;
  float: right;
  padding: 8px;
  text-align: center;
  color: #fff;
  font-weight: bold;
  font-size: 14px;
  margin: 20px 0 0;
  cursor: pointer;
}
.steps-component .steps-nav {
  float: left;
  clear: both;
}
.steps-component .steps-nav button {
  background: #50bb50;
  border: 0px;
  font-size: 21px;
  width: 50px;
  color: #fff;
  cursor: pointer;
  height: 50px;
  position: absolute;
  outline: none;
}
.steps-component .steps-nav button:disabled {
  background: #ccc;
}
.steps-component .steps-nav .steps-nav-prev {
  left: -50px;
  top: 100px;
}
.steps-component .steps-nav .steps-nav-next {
  right: -50px;
  top: 100px;
}
.intro {
  font-family: sans-serif;
  text-align: center;
}
.intro .title {
  font-size: 30px;
}
@media screen and (min-width: 232px) and (max-width: 481px) {
  .steps-component {
    width: 480px;
    height: 100%;
  }
  .steps-component .steps-navigator {
    margin: 1em 0;
  }
}
@media screen and (min-width: 701px) and (max-width: 1399px) {
  .steps-component {
    width: 700px;
  }
  .steps-component .steps-content {
    box-shadow: 0px 0px 8px #ccc;
  }
  .steps-component .steps-navigator {
    margin: 2em 0;
  }
}
@media screen and (min-width: 1400px) {
  .steps-component {
    width: 50vw;
  }
}
.-rs-icon-connected {
  background-image: url("https://static.redsift.io/assets/sifts/sift-connected/icon.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.-rs-icon-angellist {
  background-image: url("https://static.redsift.io/assets/sifts/sift-connected/angellist.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.-rs-icon-twitter {
  background-image: url("https://static.redsift.io/assets/sifts/sift-connected/twitter.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.rs-icon-btn {
  width: 15em;
  height: 40px;
  margin: 2em;
}
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  text-align: center;
}
.page p {
  margin: 0.7em;
}
.page__header-icon {
  width: 1.5em;
  height: 1.5em;
  margin: 0.7em 0;
  margin-bottom: 0.2em;
}
.page__heading {
  display: none;
  margin: 2em;
}
.page__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.5em 0;
}
.page__description {
  display: none;
  margin-top: 0;
}
.page__buttons {
  display: flex;
  justify-content: center;
}
.rs-icon-btn {
  width: 100%;
  margin: 0;
}
.rs-icon-btn--selected {
  color: #fff;
  background: #0ab93a;
}
.identity-card {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 20em;
  margin: 0;
  padding: 0;
  margin-bottom: 0.2em;
  border: 1px solid #808080;
}
.identity-card--disconnected {
  margin-bottom: 0.2em;
}
.identity-card__avatar {
  width: 100%;
  max-width: 3em;
  margin: 1.5em 0;
  height: 3em;
}
.identity-card__icon {
  width: 2em;
  height: 2em;
  margin: 1em 0;
}
.identity-card--disconnected__description {
  text-align: center;
  margin: 0 1em;
}
@media screen and (min-width: 232px) and (max-width: 481px) {
  .page {
    width: 100%;
    height: 380px;
  }
  .page__header-icon {
    width: 1.5em;
    height: 1.5em;
    margin: 1em 0;
  }
  .page__heading {
    display: block;
    margin: 0;
  }
  .page__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    margin-top: 1.5em;
  }
  .page__description {
    display: block;
    margin-top: 0;
  }
  .page__buttons {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: -30px;
  }
}
@media screen and (min-width: 482px) {
  .page {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .page__header-icon {
    width: 3em;
    height: 3em;
    margin-top: 2em;
  }
  .page__heading {
    display: block;
    margin: 2em;
  }
  .page__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2em;
  }
  .page__description {
    display: block;
    margin-top: 0;
  }
  .page__buttons {
    display: flex;
    justify-content: center;
    margin: 2em;
  }
  .connected__federated-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .identity-card {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 50%;
    max-width: 20em;
    margin: 1.5em;
    padding: 0 2em;
    border: 1px solid #808080;
  }
  .identity-card__avatar {
    width: 100%;
    max-width: 3em;
    margin: 1.5em 0;
    height: 3em;
  }
  .identity-card__icon {
    width: 2em;
    height: 2em;
    margin: 1em 0;
  }
  .identity-card--disconnected {
    margin: 0 1em;
  }
  .identity-card--disconnected__description {
    text-align: center;
    margin: 0 1em;
  }
}
.steps-content {
  text-align: center;
}
